import React, {Fragment} from 'react';
import '../styles/Projects.css'

const Projects = () => {

    const projects = [
        {
            projectName: 'ToDoApp',
            live: 'https://k3gar.github.io/ToDoApp/',
            description: 'This is a ToDo App made it with Vanilla JavaScript. My greatest learnings in this project was about Local Storage and Session Storage in the borwser.',
            repo: 'https://github.com/k3gar/ToDoApp'
        },
        {
            projectName: 'Card Component',
            live: 'https://k3gar.github.io/test-sites/card-component/',
            description: 'This is one of my first projecs where I learned about the responsive design and how to use Media Queries',
            repo: 'https://github.com/k3gar/test-sites/tree/main/card-component'
        },
        {
            projectName: 'Google Clone',
            live: 'https://k3gar.github.io/test-sites/GoogleClone/',
            description: 'I made this Google Clone has a project of a course about HTML and CSS',
            repo: 'https://github.com/k3gar/test-sites/tree/main/GoogleClone'
        },
        {
            projectName: 'Profile Card Component Main',
            live: 'https://k3gar.github.io/test-sites/Profile%20Card%20Component%20Main/',
            description: 'In this project I learned about z-index and how to use it in CSS',
            repo: 'https://github.com/k3gar/test-sites/tree/main/Profile%20Card%20Component%20Main'
        },
        {
            projectName: 'Batatabit',
            live: 'https://k3gar.github.io/test-sites/Responsive-Design/',
            description: 'This was the project of a course about HTML and CSS. I learned more about how to use flexbox properly',
            repo: 'https://github.com/k3gar/test-sites/tree/main/Responsive-Design'
        },
        {
            projectName: 'SunnysideAgency',
            live: 'https://k3gar.github.io/test-sites/SunnysideAgency/',
            description: 'A good Frontend Mentor Challenge. I improved my CSS knowledge ',
            repo: 'https://github.com/k3gar/test-sites/tree/main/SunnysideAgency'
        },
        {
            projectName: 'TipCalculatorApp',
            live: 'https://k3gar.github.io/test-sites/TipCalculatorApp/',
            description: 'This is a calculator where you can calculate tips to pay',
            repo: 'https://github.com/k3gar/test-sites/tree/main/TipCalculatorApp'
        }

    ];

  return <Fragment>
      <main>
        <h1>My Projects</h1> 
          <ul className='proyectList'>
            {
                projects.map(item => (
                    <li className='proyectList_item' key={item.key}>
                        <h2>{item.projectName}</h2>
                        <p>{item.description}</p>
                        <a href={item.live} className='proyectList_links' >Live Project</a>
                        <a href={item.repo} className='proyectList_links'>Repo</a>
                        
                    </li>
                ))
            }

          </ul>
      </main>
  </Fragment>;
};

export default Projects;
