import React from 'react';
import '../styles/About.css'
const About = () => {
    return (
        <main className="About-Main">
            <h1>ABOUT ME</h1>
            <p>Desde hace unos años la tecnología ha formado parte de mi vida, sin embargo, tengo una historia en el medio que quisiera contar.</p>
            <p>El mundo del arte llegó a mi vida hace un tiempo, participé en eventos de Clown donde aprendí a maravillarme con lo simple, ante las pequeñas cosas. La vida me llevó por otros caminos, sin embargo, decidí adoptar esa filosofía, que va más allá de aprender algo constantemente, sino de disfrutar profundamente y permitirme asombrarme al descubrir que hay cosas que no entiendo, pero que puedo entender.</p>
            <p>A lo largo de este tiempo mi experiencia ha girado al rededor de las redes informáticas, sin embargo mi espíritu de aventura me ha hecho el llamado a buscar nuevos rumbos, así que inicié en el mundo del desarrollo web donde he encontrado una gran curiosidad al cada día descubrir que existe algo nuevo que aprender, y he decidido que es aquí, donde por ahora liberar mi potencial y permitir fluir mi parte creativa con la parte en mí que ama la programación.</p>
            <p>Al igual que a muchos, el 2020 me llenó de ansiedades, incertidumbres y miedos, así mismo, el vivir sucesos de violencia típicos en Latinoamérica me hicieron ser más consciente de una verdad. Voy a morir, podría ser en 30 años, así como podría ser hoy mismo, y no importa cuando suceda, lo que sí me importa es que en ese momento sienta que pude maravillarme, hacer grandes cosas y sobre todo, que fue la vida que quise vivir</p>
            <p><b>Hay infinidad de mundos allá afuera, y voy a recorrer los que la vida me permita.</b></p>
        </main>
    )
}

export default About
